import { apiInstance } from "./index.js";

const api = apiInstance();

function listWiki(success, fail) {
  api.get(`/wiki/`).then(success).catch(fail);
}

function titleSearch(params, success, fail) {
  api.get(`/wiki/title`, { params }).then(success).catch(fail);
}

function keywordSearch(params, success, fail) {
  api.get(`/wiki/keyowrd`, { params }).then(success).catch(fail);
}

function writeWiki(title, body, success, fail) {
  api.post(`/wiki/create`, { title, body }).then(success).catch(fail);
}

function modifyWiki(title, body, success, fail) {
  api.put(`/wiki/update/${uri}`, { title, body }).then(success).catch(fail);
}

function deleteWiki(no, success, fail) {
  api.delete(`/wiki/delete/${no}`).then(success).catch(fail);
}

export {
  listWiki,
  titleSearch,
  keywordSearch,
  writeWiki,
  modifyWiki,
  deleteWiki,
};
