<template>
  <card class="card" title="사용자 정보">
    <div>
      <form @submit.prevent>
        <div class="row">
          <div class="col-md-12">
            <fg-input
              type="text"
              label="ID"
              :disabled="true"
              placeholder="ID"
              v-model="copy.id"
            >
            </fg-input>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12">
            <fg-input
              type="password"
              label="Password"
              placeholder="Password"
              v-model="copy.password"
            >
            </fg-input>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12">
            <fg-input
              type="text"
              label="Name"
              placeholder="Name"
              v-model="copy.name"
            >
            </fg-input>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12">
            <fg-input
              type="text"
              label="E-mail"
              placeholder="E-mail"
              v-model="copy.email"
            >
            </fg-input>
          </div>
        </div>

        <div class="row">
          <div class="col-md-12 col-12">
            <p class="text-muted">Address</p>
            <b-input
              type="text"
              label="Address"
              placeholder="주소 입력후 엔터"
              v-model="copy.address"
              @keyup.enter="check()"
            >
            </b-input>
          </div>
        </div>
        <div class="text-center">
          <p-button type="info" round @click.native.prevent="update()">
            계정 정보 수정
          </p-button>
        </div>
        <br />
        <div class="text-center">
          <p-button
            type="danger"
            round
            @click.native.prevent="deleteUser(user.id)"
          >
            계정 삭제
          </p-button>
        </div>

        <div class="clearfix"></div>
      </form>
    </div>
  </card>
</template>
<script>
var lat = "";
var lng = "";
import { mapActions, mapState } from "vuex";

export default {
  name: "MemberInfo",
  data() {
    return {
      copy: {},
      my_api_key: "ccfa376a58b4257a8d08fd422f80329c",
    };
  },
  created() {
    this.copy["id"] = this.user.id;
    this.copy["name"] = this.user.name;
    this.copy["password"] = this.user.password;
    this.copy["email"] = this.user.email;
    this.copy["address"] = this.user.address;
  },
  computed: {
    ...mapState("memberStore", ["user"]),
  },
  methods: {
    check() {
      var geocoder = new kakao.maps.services.Geocoder();
      geocoder.addressSearch(this.copy.address, function (result, status) {
        if (status === kakao.maps.services.Status.OK) {
          lat = result[0].y;
          lng = result[0].x;
        }
      });
      alert("정상적인 주소입니다.");
    },
    update() {
      const user = {
        id: this.copy.id,
        name: this.copy.name,
        password: this.copy.password,
        email: this.copy.email,
        address: this.copy.address,
        lat: lat,
        lng: lng,
      };
      this.updateUser(user);
    },
    ...mapActions("memberStore", ["deleteUser", "updateUser", "getInfo"]),
  },
  mounted() {
    const script = document.createElement("script");
    script.onload = () => kakao.maps.load(this.initMap);
    script.src =
      "http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=" +
      this.my_api_key +
      "&libraries=services";
    document.head.appendChild(script);
  },
};
</script>
