import {
  aptSearchList,
  dongSearchList,
  aptNewsList,
  sidoList,
  gugunList,
  dongList,
  popularDongList,
  popularAptList,
} from "@/api/apt.js";

const aptStore = {
  namespaced: true,
  state: {
    sidos: [{ value: null, text: "선택하세요" }],
    guguns: [{ value: null, text: "선택하세요" }],
    dongs: [{ value: null, text: "선택하세요" }],
    apts: [],
    apt: null,
    popularApts: [],
    popularDongs: [],
    preferencesChart: {
      data: {
        series: [],
        label: [],
      },
      options: {},
    },
    preferencesChart2: {
      data: {
        series: [],
        label: [],
      },
      options: {},
    },
    newsArray: [],
  },

  getters: {},

  mutations: {
    SET_SIDO_LIST: (state, sidos) => {
      sidos.forEach((sido) => {
        state.sidos.push({ value: sido.sidoCode, text: sido.sidoName });
      });
    },
    SET_GUGUN_LIST: (state, guguns) => {
      guguns.forEach((gugun) => {
        state.guguns.push({ value: gugun.gugunCode, text: gugun.gugunName });
      });
    },
    SET_DONG_LIST: (state, dongs) => {
      dongs.forEach((dong) => {
        state.dongs.push({ value: dong.dongCode, text: dong.dongName });
      });
    },
    SET_APT_LIST: (state, apts) => {
      state.apts = apts;
    },
    SET_POPULAR_APTS: (state, apts) => {
      state.popularApts = apts;
    },
    SET_POPULAR_DONGS: (state, dongs) => {
      state.popularDongs = dongs;
    },
    CLEAR_SIDO_LIST: (state) => {
      state.sidos = [{ value: null, text: "선택하세요" }];
    },
    CLEAR_GUGUN_LIST: (state) => {
      state.guguns = [{ value: null, text: "선택하세요" }];
    },
    CLEAR_LIST: (state) => {
      state.apts = [];
    },
    SET_DETAIL_HOUSE: (state, house) => {
      state.house = house;
    },
    SET_CHART: (state) => {
      state.preferencesChart.data.series.push(state.popularDongs[0].dongCount);
      state.preferencesChart.data.series.push(state.popularDongs[1].dongCount);
      state.preferencesChart.data.series.push(state.popularDongs[2].dongCount);
      state.preferencesChart.data.series.push(state.popularDongs[3].dongCount);
    },
    SET_CHART2: (state) => {
      state.preferencesChart2.data.series.push(state.popularApts[0].aptCount);
      state.preferencesChart2.data.series.push(state.popularApts[1].aptCount);
      state.preferencesChart2.data.series.push(state.popularApts[2].aptCount);
      state.preferencesChart2.data.series.push(state.popularApts[3].aptCount);
    },
    SET_NEWS_LIST: (state, newsList) => {
      newsList.forEach((news) => {
        state.newsArray.push({
          title: news.title,
          url:
            "https://land.naver.com/news/newsRead.naver?type=field&amp;news_type_cd=30&amp;prsco_id=014&amp;arti_id=" +
            news.url,
        });
      });
    },
    CLEAR_CHART: (state) => {
      state.preferencesChart.data.series = [];
      state.preferencesChart2.data.series = [];
      state.popularDongs = [];
      state.popularApts = [];
      state.newsArray = [];
    },
  },

  actions: {
    getAptSearch: ({ commit }, name) => {
      let param = {
        aptName: name,
      };
      aptSearchList(
        param,
        ({ data }) => {
          console.log(data);
          commit("SET_APT_LIST", data);
        },
        (error) => {
          console.log(error);
        }
      );
    },
    getDongSearch: ({ commit }, dongCode) => {
      let param = {
        dong: dongCode,
      };
      dongSearchList(
        param,
        ({ data }) => {
          commit("SET_APT_LIST", data);
        },
        (error) => {
          console.log(error);
        }
      );
    },
    getSido: ({ commit }) => {
      sidoList(
        ({ data }) => {
          // console.log(data);
          commit("SET_SIDO_LIST", data);
        },
        (error) => {
          console.log(error);
        }
      );
    },
    getGugun: ({ commit }, sidoCode) => {
      const params = {
        sido: sidoCode,
      };
      gugunList(
        params,
        ({ data }) => {
          commit("SET_GUGUN_LIST", data);
        },
        (error) => {
          console.log(error);
        }
      );
    },
    getDong: ({ commit }, gugunCode) => {
      const params = {
        gugun: gugunCode,
      };
      dongList(
        params,
        ({ data }) => {
          commit("SET_DONG_LIST", data);
        },
        (error) => {
          console.log(error);
        }
      );
    },
    getChart({ commit }) {
      popularDongList(
        ({ data }) => {
          commit("SET_POPULAR_DONGS", data);
          commit("SET_CHART");
        },
        (error) => {
          console.log(error);
        }
      );
    },
    getChart2({ commit }) {
      popularAptList(
        ({ data }) => {
          commit("SET_POPULAR_APTS", data);
          commit("SET_CHART2");
        },
        (error) => {
          console.log(error);
        }
      );
    },
    getNews({ commit }) {
      aptNewsList(
        ({ data }) => {
          commit("SET_NEWS_LIST", data);
        },
        (error) => {
          console.log(error);
        }
      );
    },
  },
};

export default aptStore;
