import {
  listWiki,
  titleSearch,
  keywordSearch,
  writeWiki,
  modifyWiki,
  deleteWiki,
} from "@/api/wiki.js";

const wikiStore = {
  namespaced: true,
  state: {
    list: [],
    select: {},
  },

  getters: {},

  mutations: {
    SET_LIST: (state, data) => {
      state.list = data;
    },
    SET_SELECT: (state, data) => {
      state.select = data;
    },
  },
  actions: {
    getlist: ({ commit }) => {
      listWiki(
        ({ data }) => {
          commit("SET_LIST", data);
        },
        ({ error }) => {
          console.log(error);
        }
      );
    },
    setSelect: ({ commit }, item) => {
      commit("SET_SELECT", item);
    },
    delete: (no) => {
      deleteWiki(
        no,
        ({}) => {
          alert("문서가 삭제되었습니다.");
        },
        ({ error }) => {
          console.log(error);
        }
      );
    },
  },
};

export default wikiStore;
