import { apiInstance } from "./index.js";

const api = apiInstance();

function listArticle(success, fail) {
  api.get(`/qna/questions`).then(success).catch(fail);
}

function selectArticle(no, success, fail) {
  api.get(`/qna/${no}`).then(success).catch(fail);
}

function getAnswerArticles(no, success, fail) {
  api.get(`/qna/${no}/answers`).then(success).catch(fail);
}

function searchArticle(params, success, fail) {
  api.get(`/qna/search`, { params: params }).then(success).catch(fail);
}

function writeArticle(article, success, fail) {
  api.defaults.headers["access-token"] = sessionStorage.getItem("access-token");
  api.post(`/qna/question`, JSON.stringify(article)).then(success).catch(fail);
}

function writeAnswerArticle(article, success, fail) {
  api.defaults.headers["access-token"] = sessionStorage.getItem("access-token");
  api.post(`/qna/answer`, JSON.stringify(article)).then(success).catch(fail);
}

function modifyArticle(article, success, fail) {
  api.defaults.headers["access-token"] = sessionStorage.getItem("access-token");
  api.put(`/qna/update`, JSON.stringify(article)).then(success).catch(fail);
}

function deleteArticle(no, success, fail) {
  api.defaults.headers["access-token"] = sessionStorage.getItem("access-token");
  api.delete(`/qna/${no}`).then(success).catch(fail);
}

export {
  listArticle,
  selectArticle,
  getAnswerArticles,
  searchArticle,
  writeArticle,
  writeAnswerArticle,
  modifyArticle,
  deleteArticle,
};
