import { apiInstance /*aptInstance*/ } from "./index.js";

const api = apiInstance();
//const house = houseInstance();

function dongSearchList(params, success, fail) {
  api.get(`/map/apt`, { params: params }).then(success).catch(fail);
}

function aptSearchList(params, success, fail) {
  api.get(`/map/apt2`, { params: params }).then(success).catch(fail);
}

function aptNewsList(success, fail) {
  api.get(`/news/`).then(success).catch(fail);
}

function sidoList(success, fail) {
  api.get(`/map/sido`).then(success).catch(fail);
}

function gugunList(params, success, fail) {
  api.get(`/map/gugun`, { params: params }).then(success).catch(fail);
}

function dongList(params, success, fail) {
  api.get(`/map/dong`, { params: params }).then(success).catch(fail);
}

function popularDongList(success, fail) {
  api.get(`/map/popularDong`).then(success).catch(fail);
}

function popularAptList(success, fail) {
  api.get(`/map/popularApt`).then(success).catch(fail);
}

export {
  aptSearchList,
  dongSearchList,
  aptNewsList,
  sidoList,
  gugunList,
  dongList,
  popularDongList,
  popularAptList,
};
