<template>
  <div v-html="compiledHtml">
    <div></div>
  </div>
</template>
<script>
import axios from "axios";

export default {
  data: function () {
    return {
      url: "terms.html",
      input: "",
    };
  },
  created() {
    this.url = this.$route.params.uri;
    this.loadFile();
  },
  computed: {
    compiledHtml: function () {
      return this.input;
    },
  },
  methods: {
    loadFile() {
      axios
        .get("http://localhost" + this.url)
        .then((result) => {
          this.input = result.data;
        })
        .catch((error) => {
          console.error("error getting file");
        });
    },
  },
};
</script>
<style></style>
