<template>
  <b-container class="bv-example-row mt-3">
    <b-row class="mb-1">
      <b-col>
        <b-input
          type="text"
          v-model="title"
          placeholder="검색어 입력 후 엔터"
          @keyup.enter="search()"
        />
      </b-col>
      <b-col class="text-right">
        <b-button variant="outline-primary" @click="moveWrite()"
          >글쓰기</b-button
        >
      </b-col>
    </b-row>
    <b-row>
      <b-col v-if="articles.length">
        <b-table-simple hover responsive>
          <b-thead head-variant="dark">
            <b-tr>
              <b-th>글번호</b-th>
              <b-th>제목</b-th>
              <b-th>작성일</b-th>
              <b-th>작성자</b-th>
            </b-tr>
          </b-thead>
          <tbody>
            <!-- 하위 component인 ListRow에 데이터 전달(props) -->
            <qna-list-row
              v-for="(article, index) in articles"
              :key="index"
              v-bind="article"
            />
          </tbody>
        </b-table-simple>
      </b-col>
      <!-- <b-col v-else class="text-center">도서 목록이 없습니다.</b-col> -->
    </b-row>
  </b-container>
</template>

<script>
import QnaListRow from "@/components/Qna/Child/QnaListRow";
import { mapState, mapActions, mapMutations } from "vuex";

export default {
  name: "Qna",
  components: {
    QnaListRow,
  },
  data() {
    return {
      title: "",
    };
  },
  computed: {
    ...mapState("qnaStore", ["articles"]),
  },
  created() {
    this.getList();
    console.log(this.articles);
    this.CLEAR_ARTICLE_LIST();
  },
  methods: {
    moveWrite() {
      this.$router.push({ name: "QnaWrite" });
    },
    ...mapMutations("qnaStore", ["CLEAR_ARTICLE_LIST"]),
    ...mapActions("qnaStore", ["getSearch"]),
    search() {
      this.CLEAR_ARTICLE_LIST();
      this.getSearch(this.title);
    },
    ...mapActions("qnaStore", ["getList"]),
  },
};
</script>

