<template>
  <!--새거-->
  <div>
    <form @submit.prevent>
      <div class="row">
        <div class="col-md-12">
          <fg-input type="text" label="ID" placeholder="ID" v-model="user.id"> </fg-input>
        </div>
      </div>
      <div class="row">
        <div class="col-md-12">
          <fg-input
            type="password"
            label="Password"
            placeholder="Password"
            v-model="user.password"
          >
          </fg-input>
        </div>
      </div>

      <div class="text-center">
        <p-button type="info" round @click.native.prevent="log(user)"> 로그인 </p-button>
      </div>
      <div class="clearfix"></div>
    </form>
  </div>
</template>

<script>
import { mapActions, } from "vuex";

export default {
  name: "MemberLogin",
  data() {
    return {
      user: {
        id: "",
        password: "",
      },
    };
  },
  methods: {
    ...mapActions("memberStore", ["login"]),
    log(user) {
      console.log(user);
      this.login(user);
    },
    signUp() {
      this.$router.push({ name: "SignUp" });
    },
  },
};
</script>

