<template>
  <div>
    <table>
      <thead>
        <th>번호</th>
        <th>이름</th>
      </thead>
      <tbody>
        <tr v-for="item in list" :key="item.no">
          <td>{{ item.no }}</td>
          <td>
            <router-link
              :to="{ name: 'WikiDetail', params: { uri: item.uri } }"
            >
              {{ item.title }}
            </router-link>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
import axios from "axios";

