<template>
  <div>
    <!-- property area -->
    <div
      class="content-area recent-property"
      style="padding-bottom: 60px; background-color: rgb(252, 252, 252)"
    >
      <div class="container">
        <div
          class="col-lg-9 col-lg-offset-2 col-md-10 col-md-offset-1 col-sm-12"
        >
          <div class="search-form wow pulse" data-wow-delay="0.8s">
            <div class="form-inline">
              <div class="form-group">
                <b-select name="sido" v-model="sidoCode">
                  <option
                    v-for="sido in sidos"
                    :key="sido.value"
                    :value="sido.value"
                  >
                    {{ sido.text }}
                  </option>
                </b-select>

                <b-select name="gugun" v-model="gugunCode">
                  <option
                    v-for="gugun in guguns"
                    :key="gugun.value"
                    :value="gugun.value"
                  >
                    {{ gugun.text }}
                  </option>
                </b-select>

                <b-select name="dong" v-model="dongCode">
                  <option
                    v-for="dong in dongs"
                    :key="dong.value"
                    :value="dong.value"
                  >
                    {{ dong.text }}
                  </option>
                </b-select>

                <b-button @click="getDongList">검색</b-button>
              </div>
            </div>
          </div>
          <div id="kakaomap" style="width: 100%; height: 500px"></div>

          <b-table-simple hover responsive>
            <b-thead head-variant="dark">
              <b-tr>
                <b-th>번호</b-th>
                <b-th>아파트</b-th>
                <b-th class="text-center">주소</b-th>
                <b-th>건축연도</b-th>
                <b-th>최근거래금액</b-th>
                <b-th>주소와의 거리(km)</b-th>
              </b-tr>
            </b-thead>
            <b-tbody id="searchResult">
              <b-tr v-for="vo in apts" :key="vo.aptCode">
                <b-td>{{ vo.aptCode }}</b-td>
                <b-td>{{ vo.aptName }}</b-td>
                <b-td>
                  {{ vo.sidoName }} {{ vo.gugunName }} {{ vo.dongName }}
                  {{ vo.jibun }}
                </b-td>
                <b-td>{{ vo.buildYear }}</b-td>
                <b-td>{{ vo.recentPrice }}</b-td>
                <b-td>{{ vo.distance }}</b-td>
              </b-tr>
            </b-tbody>
          </b-table-simple>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import axios from "axios";
export default {
  name: "HouseSearchBar",
  data() {
    return {
      sidoCode: null,
      sidos: [{ value: null, text: "선택하세요" }],
      gugunCode: null,
      guguns: [{ value: null, text: "선택하세요" }],
      dongCode: null,
      dongs: [{ value: null, text: "선택하세요" }],
      apts: [],
      my_api_key: "ccfa376a58b4257a8d08fd422f80329c",
    };
  },
  created() {
    axios
      .get("http://localhost/map/sido")
      .then((response) => {
        response.data.forEach((sido) => {
          this.sidos.push({ value: sido.sidoCode, text: sido.sidoName });
        });
      })
      .catch((error) => {
        console.log(error);
      });
  },
  watch: {
    sidoCode() {
      axios
        .get("http://localhost/map/gugun?sido=" + this.sidoCode)
        .then((response) => {
          this.guguns = [{ value: null, text: "선택하세요" }];
          this.dongs = [{ value: null, text: "선택하세요" }];
          response.data.forEach((gugun) => {
            this.guguns.push({ value: gugun.gugunCode, text: gugun.gugunName });
          });
        })
        .catch((error) => {
          console.log(error);
        });
    },
    gugunCode() {
      axios
        .get("http://localhost/map/dong?gugun=" + this.gugunCode)
        .then((response) => {
          this.dongs = [{ value: null, text: "선택하세요" }];
          response.data.forEach((dong) => {
            this.dongs.push({ value: dong.dongCode, text: dong.dongName });
          });
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  computed: {
    ...mapState("memberStore", ["user"]),
  },
  methods: {
    displayMarkers(list) {
      const container = document.querySelector("#kakaomap");
      let options = null;
      if (list.length > 0) {
        options = {
          center: new kakao.maps.LatLng(list[0].lat, list[0].lng),
          level: 3,
        };
      } else {
        options = {
          center: new kakao.maps.LatLng(35.19656853772262, 129.0807270648317),
          level: 3,
        };
      }

      const map = new kakao.maps.Map(container, options);
      list.forEach((apt) => {
        let markerPosition = new kakao.maps.LatLng(apt.lat, apt.lng);
        const marker = new kakao.maps.Marker({
          position: markerPosition,
        });
        marker.setMap(map);
      });
    },
    getDongList() {
      console.log("getAptList");
      axios
        .get(
          "http://localhost/map/apt?dong=" +
            this.dongCode +
            "&&id=" +
            this.user.id
        )
        .then((response) => {
          console.log(response);
          this.apts = response.data;
          this.displayMarkers(this.apts);
        });
    },
    initMap() {
      const container = document.querySelector("#kakaomap");
      let options = {
        center: new kakao.maps.LatLng(35.19656853772262, 129.0807270648317),
        level: 3,
      };
      const map = new kakao.maps.Map(container, options);
      const markerPosition = new kakao.maps.LatLng(
        35.19656853772262,
        129.0807270648317
      );

      const marker = new kakao.maps.Marker({
        position: markerPosition,
      });
      marker.setMap(map);
    },
    sendKeyword() {},
  },
  mounted() {
    if (window.kakao && window.kakao.maps) {
      this.initMap();
    } else {
      const script = document.createElement("script");
      script.onload = () => kakao.maps.load(this.initMap);
      script.src =
        "http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=" +
        this.my_api_key;
      document.head.appendChild(script);
    }
  },
};
</script>

<style></style>
