<template>
  <b-container class="bv-example-row mt-3">
    <b-row class="mb-1">
      <b-col class="text-right">
        <b-button
          variant="outline-info"
          size="sm"
          @click="moveModifyArticle"
          class="mr-2"
          >글수정</b-button
        >
        <b-button variant="outline-danger" size="sm" @click="deleteArticle"
          >글삭제</b-button
        >
      </b-col>
    </b-row>
    <b-row class="mb-1">
      <b-col>
        <b-card
          bg-variant="light"
          :header-html="`<h3>
          ${article.title} </h3><div><h6>${article.id}</div><div>${article.date}</h6></div>`"
          class="mb-2"
          border-variant="dark"
          no-body
        >
          <b-card-body class="text-left">
            <div v-html="message"></div>
          </b-card-body>
          <b-button size="sm" @click="answerArticle">답변 달기</b-button>
        </b-card>
      </b-col>
    </b-row>
    <b-row class="mb-1" v-for="answer in answers" :key="answer.no">
      <b-col>
        <b-card
          bg-variant="light"
          :header-html="`<h3>
          ${answer.title} </h3><div><h6>${answer.id}</div><div>${answer.date}</h6></div>`"
          class="mb-2"
          border-variant="dark"
          no-body
        >
          <b-card-body class="text-left">
            <div>{{ answer.content }}</div>
          </b-card-body>
        </b-card>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
// import moment from "moment";
import { mapActions, mapMutations, mapState } from "vuex";

export default {
  data() {
    return {
      no: this.$route.params.no,
    };
  },
  computed: {
    ...mapState("qnaStore", ["article"]),
    ...mapState("qnaStore", ["answers"]),
    ...mapState("memberStore", ["confirmId"]),
    message() {
      if (this.article.content)
        return this.article.content.split("\n").join("<br>");
      return "";
    },
  },
  created() {
    this.getSelected(this.no);
    this.getAnswers(this.no);
    this.CLEAR_SELECTED_LIST();
    this.CLEAR_ANSWER_LIST();
  },
  methods: {
    ...mapActions("qnaStore", ["getAnswers"]),
    ...mapActions("qnaStore", ["getSelected"]),
    ...mapMutations("qnaStore", ["CLEAR_SELECTED_LIST"]),
    ...mapMutations("qnaStore", ["CLEAR_ANSWER_LIST"]),
    listArticle() {
      this.$router.push({ name: "QnaList" });
    },
    moveModifyArticle() {
      if (this.article.id == this.confirmId) {
        this.$router.replace({
          name: "QnaUpdate",
          params: { no: this.article.no },
        });
      } else {
        alert("본인 게시글이 아닙니다.");
      }
    },
    deleteArticle() {
      if (confirm("정말로 삭제?")) {
        if (this.article.id == this.confirmId) {
          this.$router.replace({
            name: "QnaDelete",
            params: { no: this.article.no },
          });
        } else {
          alert("본인 게시글이 아닙니다.");
        }
      }
    },
    answerArticle() {
      this.$router.replace({
        name: "QnaAnswer",
        params: { no: this.article.no },
      });
    },
  },
};
</script>

<style></style>