import {
  login,
  findById,
  deleteUser,
  updateUser,
  addUser,
} from "@/api/member.js";
import router from "@/router/index";

const memberStore = {
  namespaced: true,
  state: {
    user: {
      id: "",
      password: "",
      name: "",
      email: "",
      address: "",
    },
    curState: "",
    confirmId: "",
  },

  getters: {},

  mutations: {
    SET_USER: (state, data) => {
      console.log(data.userInfo);
      state.user["id"] = data.userInfo["id"];
      state.user["password"] = data.userInfo["password"];
      state.user["name"] = data.userInfo["name"];
      state.user["email"] = data.userInfo["email"];
    },
    SET_STATE: (state, data) => {
      state.curState = data.message;
    },
    SET_ID: (state, id) => {
      state.confirmId = id;
    },
  },

  actions: {
    login: ({ commit }, user) => {
      login(
        {
          id: user.id,
          password: user.password,
        },
        ({ data }) => {
          commit("SET_ID", user.id);
          sessionStorage.setItem("access-token", data["access-token"]);
          commit("SET_STATE", data);

          findById(
            user.id,
            ({ data }) => {
              commit("SET_USER", data);
              commit("SET_STATE", data);
              alert("로그인에 성공하였습니다.");
              router.push("/layout");
            },
            (error) => {
              commit("SET_USER", { userInfo: {} });
              alert("로그인에 실패하였습니다.");
              console.log(error);
            }
          );
        },
        (error) => {
          console.log(error);
        }
      );
    },
    getInfo: ({ commit }, id) => {
      findById(
        id,
        ({ data }) => {
          commit("SET_USER", data);
          commit("SET_STATE", data);
        },
        (error) => {
          commit("SET_USER", { userInfo: {} });
          console.log(error);
        }
      );
    },
    deleteUser: ({ commit }, id) => {
      deleteUser(
        id,
        () => {
          sessionStorage.setItem("access-token", "");
          commit("SET_STATE", "fail");
          alert("계정을 정상적으로 삭제하였습니다.");
          router.push({ name: "login" });
        },
        (error) => {
          console.log(error);
          alert("계정 삭제 중 문제가 발생하였습니다.");
        }
      );
    },
    updateUser: ({ commit }, user) => {
      updateUser(
        user,
        ({ data }) => {
          commit("SET_STATE", data);
          findById(
            user.id,
            ({ data }) => {
              commit("SET_USER", data);
              commit("SET_STATE", data);
              alert("정보를 정상적으로 수정하였습니다.");
              router.push("/layout/stats");
            },
            (error) => {
              commit("SET_USER", { userInfo: {} });
              alert("정보 수정 중 문제가 발생하였습니다.");
              console.log(error);
            }
          );
        },
        (error) => {
          console.log(error);
        }
      );
    },
    add: ({ commit }, user) => {
      addUser(
        user,
        () => {
          commit("SET_USER", { userInfo: {} });
          commit("SET_STATE", "fail");
          alert("회원가입에 성공하였습니다.");
          router.push({ name: "login" });
        },
        (error) => {
          alert("회원가입에 실패하였습니다.");
          console.log(error);
        }
      );
    },
    logout: ({ commit }) => {
      sessionStorage.setItem("access-token", "");
      commit("SET_USER", { userInfo: {} });
      commit("SET_STATE", "fail");
      router.push({ name: "login" });
    },
  },
};

export default memberStore;
