<template>
  <div>
    <!-- property area -->

    <div
      class="content-area recent-property"
      style="padding-bottom: 60px; background-color: rgb(252, 252, 252)"
    >
      <div class="container">
        <div
          class="col-lg-9 col-lg-offset-2 col-md-10 col-md-offset-1 col-sm-12"
        >
          <div class="search-form wow pulse" data-wow-delay="0.8s">
            <div class="form-inline">
              <div class="form-group">
                <b-input
                  type="text"
                  id="apt"
                  name="apt"
                  size="10"
                  v-model="name"
                />

                <b-button type="button" @click="this.getAptList">검색</b-button>
              </div>
            </div>
          </div>

          <div id="kakaomap" style="width: 100%; height: 500px"></div>

          <b-table-simple hover responsive>
            <b-thead head-variant="dark">
              <b-tr>
                <b-th>번호</b-th>
                <b-th>아파트</b-th>
                <b-th class="text-center">주소</b-th>
                <b-th>건축연도</b-th>
                <b-th>최근거래금액</b-th>
                <b-th>주소와의 거리(km)</b-th>
              </b-tr>
            </b-thead>
            <b-tbody id="searchResult">
              <b-tr v-for="vo in apts" :key="vo.aptCode">
                <b-td>{{ vo.aptCode }}</b-td>
                <b-td>{{ vo.aptName }}</b-td>
                <b-td>
                  {{ vo.sidoName }} {{ vo.gugunName }} {{ vo.dongName }}
                  {{ vo.jibun }}
                </b-td>
                <b-td>{{ vo.buildYear }}</b-td>
                <b-td>{{ vo.recentPrice }}</b-td>
                <b-td>{{ vo.distance }}</b-td>
              </b-tr>
            </b-tbody>
          </b-table-simple>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import axios from "axios";
export default {
  data() {
    return {
      apts: [],
      name: "",
      my_api_key: "ccfa376a58b4257a8d08fd422f80329c",
    };
  },
  computed: {
    ...mapState("memberStore", ["user"]),
  },
  methods: {
    displayMarkers(list) {
      const container = document.querySelector("#kakaomap");
      let options = null;
      if (list.length > 0) {
        options = {
          center: new kakao.maps.LatLng(list[0].lat, list[0].lng),
          level: 3,
        };
      } else {
        options = {
          center: new kakao.maps.LatLng(35.19656853772262, 129.0807270648317),
          level: 3,
        };
      }

      const map = new kakao.maps.Map(container, options);
      list.forEach((apt) => {
        let markerPosition = new kakao.maps.LatLng(apt.lat, apt.lng);
        const marker = new kakao.maps.Marker({
          position: markerPosition,
        });
        marker.setMap(map);
      });
    },
    getAptList() {
      console.log(this.user.id);
      axios
        .get(
          "http://localhost/map/apt2?aptName=" +
            this.name +
            "&&id=" +
            this.user.id
        )
        .then((response) => {
          this.apts = response.data;
          console.log(this.apts);
          this.displayMarkers(this.apts);
        });
    },
    initMap() {
      const container = document.querySelector("#kakaomap");
      let options = {
        center: new kakao.maps.LatLng(35.19656853772262, 129.0807270648317),
        level: 3,
      };
      const map = new kakao.maps.Map(container, options);
      const markerPosition = new kakao.maps.LatLng(
        35.19656853772262,
        129.0807270648317
      );

      const marker = new kakao.maps.Marker({
        position: markerPosition,
      });
      marker.setMap(map);
    },
  },
  mounted() {
    if (window.kakao && window.kakao.maps) {
      this.initMap();
    } else {
      const script = document.createElement("script");
      script.onload = () => kakao.maps.load(this.initMap);
      script.src =
        "http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=" +
        this.my_api_key;
      document.head.appendChild(script);
    }
  },
};
</script>

<style></style>
