import {
  listArticle,
  selectArticle,
  getAnswerArticles,
  searchArticle,
  writeArticle,
  writeAnswerArticle,
  modifyArticle,
  deleteArticle,
} from "@/api/qna.js";

const qnaStore = {
  namespaced: true,
  state: {
    articles: [],
    article: {},
    answers: [],
    id: "",
  },

  getters: {},

  mutations: {
    SET_ARTICLE_LIST: (state, articles) => {
      articles.forEach((article) => {
        state.articles.push(article);
      });
    },
    SET_ARTICLE_SELECTED: (state, article) => {
      state.article = article;
    },
    SET_ARTICLE_ANSWERED: (state, article) => {
      state.answers.push(article);
    },
    SET_SEARCH_LIST: (state, articles) => {
      articles.forEach((article) => {
        state.articles.push(article);
      });
    },
    CLEAR_ARTICLE_LIST: (state) => {
      state.articles = [];
    },
    CLEAR_SELECTED_LIST: (state) => {
      state.article = {};
    },
    CLEAR_ANSWER_LIST: (state) => {
      state.answers = [];
    },
    CLEAR_ALL: (state) => {
      state.articles = [];
      state.article = {};
      state.answers = [];
    },
  },

  actions: {
    getList: ({ commit }) => {
      listArticle(
        ({ data }) => {
          commit("SET_ARTICLE_LIST", data);
        },
        (error) => {
          console.log(error);
        }
      );
    },
    getSelected: ({ commit }, no) => {
      selectArticle(
        no,
        ({ data }) => {
          commit("SET_ARTICLE_SELECTED", data);
        },
        (error) => {
          console.log(error);
        }
      );
    },
    getAnswers: ({ commit }, no) => {
      getAnswerArticles(
        no,
        ({ data }) => {
          let answers = data;
          answers.forEach((num) => {
            selectArticle(
              num,
              ({ data }) => {
                commit("SET_ARTICLE_ANSWERED", data);
              },
              (error) => {
                console.log(error);
              }
            );
          });
        },
        (error) => {
          console.log(error);
        }
      );
    },
    getSearch: ({ commit }, title) => {
      const params = {
        title: title,
      };
      searchArticle(
        params,
        ({ data }) => {
          // console.log(commit, response);
          console.log(title);
          commit("SET_SEARCH_LIST", data);
        },
        (error) => {
          console.log(error);
        }
      );
    },
    writeQuestion: ({ commit }, article) => {
      writeArticle(
        article,
        () => {
          alert("등록이 완료되었습니다.");
        },
        (error) => {
          alert("등록 과정에서 오류가 발생되었습니다.");
          console.log(error);
          console.log(commit);
        }
      );
    },
    writeAnswer: ({ commit }, article) => {
      writeAnswerArticle(
        article,
        () => {
          alert("답변이 완료되었습니다.");
        },
        (error) => {
          alert("답변 등록 과정에서 오류가 발생되었습니다.");
          console.log(error);
          console.log(commit);
        }
      );
    },
    modifyArticle: ({ commit }, article) => {
      modifyArticle(
        article,
        () => {
          alert("수정이 완료되었습니다.");
        },
        (error) => {
          alert("수정 과정에서 오류가 발생되었습니다.");
          console.log(error);
          console.log(commit);
        }
      );
    },
    deleteArticle: ({ commit }, no) => {
      deleteArticle(
        no,
        () => {
          alert("삭제가 완료되었습니다.");
        },
        (error) => {
          alert("삭제 과정에서 오류가 발생되었습니다.");
          console.log(error);
          console.log(commit);
        }
      );
    },
    detailHouse: ({ commit }, house) => {
      // 나중에 house.일련번호를 이용하여 API 호출
      commit("SET_DETAIL_HOUSE", house);
    },
  },
};

export default qnaStore;
