<template>
  <b-row class="mb-1">
    <b-col style="text-align: left">
      <b-form @submit="onSubmit" @reset="onReset">
        <b-form-group id="userid-group" label="작성자:" label-for="id">
          <b-form-input
            id="id"
            :disabled="isUserid"
            v-model="article.id"
            type="text"
            required
            placeholder="작성자 입력..."
          ></b-form-input>
        </b-form-group>

        <b-form-group id="subject-group" label="제목:" label-for="subject">
          <b-form-input
            id="title"
            v-model="article.title"
            type="text"
            required
            placeholder="제목 입력..."
          ></b-form-input>
        </b-form-group>

        <b-form-group id="content-group" label="내용:" label-for="content">
          <b-form-textarea
            id="content"
            v-model="article.content"
            placeholder="내용 입력..."
            rows="10"
            max-rows="15"
          ></b-form-textarea>
        </b-form-group>

        <b-button
          type="submit"
          variant="primary"
          class="m-1"
          v-if="this.type === 'register'"
          >글작성</b-button
        >
        <b-button
          type="submit"
          variant="primary"
          class="m-1"
          v-if="this.type === 'modify'"
          >글수정</b-button
        >
        <b-button
          type="submit"
          variant="primary"
          class="m-1"
          v-if="this.type === 'answer'"
          >답변작성</b-button
        >
        <b-button type="reset" variant="danger" class="m-1">초기화</b-button>
      </b-form>
    </b-col>
  </b-row>
</template>

<script>
import { mapActions, mapMutations, mapState } from "vuex";

export default {
  name: "QnaWriteForm",
  data() {
    return {
      isUserid: false,
      questionNum: 0,
    };
  },
  props: {
    type: { type: String },
  },
  created() {
    this.CLEAR_ALL();
    if (this.type === "modify") {
      this.getSelected(this.$route.params.no);
    } else if (this.type === "answer") {
      this.isUserid = true;
      this.article.id = this.user.id;
      this.questionNo = this.$route.params.no;
    } else {
      this.isUserid = true;
      this.article.id = this.user.id;
    }
  },
  computed: {
    ...mapState("qnaStore", ["article"]),
    ...mapState("memberStore", ["user"]),
  },
  methods: {
    ...mapMutations("qnaStore", ["CLEAR_ALL"]),
    ...mapActions("qnaStore", [
      "getSelected",
      "writeQuestion",
      "modifyArticle",
      "writeAnswer",
    ]),
    onSubmit(event) {
      event.preventDefault();

      let err = true;
      let msg = "";
      !this.article.id &&
        ((msg = "작성자 입력해주세요"), (err = false), this.$refs.id.focus());
      err &&
        !this.article.title &&
        ((msg = "제목 입력해주세요"), (err = false), this.$refs.title.focus());
      err &&
        !this.article.content &&
        ((msg = "내용 입력해주세요"),
        (err = false),
        this.$refs.content.focus());

      if (!err) alert(msg);
      else if (this.type === "register") {
        this.registArticle();
      } else if (this.type === "modify") {
        this.modifyArticle2();
      } else if (this.type === "answer") {
        this.answerArticle();
      }
    },
    onReset(event) {
      event.preventDefault();
      this.article.no = 0;
      this.article.title = "";
      this.article.content = "";
      this.$router.push({ name: "QnaList" });
    },
    registArticle() {
      let myarticle = {
        id: this.article.id,
        title: this.article.title,
        content: this.article.content,
      };
      this.writeQuestion(myarticle);
      this.$router.push({ name: "QnaList" });
      this.$router.go();
    },
    modifyArticle2() {
      let myarticle2 = {
        no: this.article.no,
        id: this.article.id,
        title: this.article.title,
        content: this.article.content,
      };
      this.modifyArticle(myarticle2);
      this.$router.push({ name: "QnaList" });
      this.$router.go();
    },
    answerArticle() {
      let myarticle3 = {
        id: this.article.id,
        title: this.article.title,
        content: this.article.content,
        questionNo: this.questionNo,
      };
      this.writeAnswer(myarticle3);
      this.$router.push({ name: "QnaList" });
      this.$router.go();
    },
  },
};
</script>

